const H2 = (props: any) => {
  const { children, sub }: { children: React.ReactNode; sub: string } = props;

  return (
    <Typography
      variant='h2'
      mr={1}
      p={0}
      lineHeight={1.5}
      sx={{
        '&::before': {
          content: `"${sub}"`,
          display: 'block',
          color: 'primary.main',
          mb: 0.5,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Typography>
  );
};

const Transition = ({
  children,
  delay = 0
}: {
  children: React.ReactNode;
  delay?: number;
}) => {
  const { ref, inView } = useInView({
    // 画面の上下中央を inview の基準線にする
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

